<template>
  <div id="study-section">
    <header>
      <img @click="gohome" src="../../assets/imgs/images/pre.png" />
    </header>
    <div class="content">
      <ModuleType2 :moduleObj="obj"></ModuleType2>
    </div>
  </div>
</template>

<script>
import ModuleType2 from "@/components/ModuleType2.vue";
export default {
  components: { ModuleType2 },
  name: "StudySection",
  data() {
    return {
      obj: {},
    };
  },
  created() {
   this.$nextTick(()=>{
     this.getData();
   })
  },
  methods: {
    gohome() {
      this.$router.back(-1);
    },
    getData() {
      if (this.$route.params.obj == undefined) {
        this.obj = JSON.parse(sessionStorage.getItem("Date"));
      } else {
        this.obj = this.$route.params.obj;
        sessionStorage.setItem("Date", JSON.stringify(this.obj));
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#study-section {
  width: 100%;
  header {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background-color: #fff;
    margin: 0 20px;
    height: 50px;
    // padding: 5px 15px;
    img {
      margin-top: 10px;
      width: 10px;
      // margin-right: 10px;
    }
  }
  .content {
    margin-top: 50px;
  }
}
</style>